@import '~antd/dist/antd.css';
*{
    margin: 0;
    padding: 0;
}
ul,li{
    list-style: none;
}

body{
    background-color: #ddd;
}

a{
    text-decoration: none;
    color:black;
  }

/*Header*/
.headerwrap{
    height: 50px;
    background-color: #444;
}
.headerwrap header{
    width: 1180px;
    margin: 0 auto;
}
.headerwrap img{
    margin-top: 10px;
    width: 118px;
    float: left;
}
.headerwrap a{
    margin-left: 18px;
    color: #fff;
}
header input{
    float: left;
    margin:15px 10px 0 10px;
}

/*main*/
.main{
    width: 1180px;
    margin: 29px auto;
}
.content{
    width: 850px;
    float: left;
    /* border:2px solid red; */
    /* background-color: #fff; */
}
.sider{
    width: 300px;
    height: 150px;
    float: right;
    background-color: #fff;
    padding-left:30px;
    padding-top:20px;
}
/*home*/

.home-top {
    height: 40px;
    background-color:#f6f6f6;
}
.home-top a{
    line-height: 40px;
    margin:0 10px;
    color: #80bd01;
}
.home-list{
    background-color: #fff;
}

.topp{
    width:100%;
    height:29px;
    background:#F0F0F0;
    padding: 6px 16px 6px;
  }
  .login{
